.banner {
    color: white;
    object-fit: cover;
    height: 500px;
    margin-top: 1px;
}

.banner__contents {
    margin-left: 40px;
    padding-top: 200px;
    height: 190px;
}

.banner__title {
    font-style: 3rem;
    font-weight: 800;
    padding-bottom: 0.3rem;
}

.banner__description {
    width: 25vw;
    line-height: 1.3;
    padding-top: 1rem;
    font-size: 1rem;
    min-width: 400px;
    height: 80px;
}

.banner__button {
    cursor: pointer;
    color: #fff;
    outline: none;
    border: none;
    font-weight: 700;
    border-radius: 0.2vw;
    padding: 0.5rem 2rem;
    margin-right: 1rem;
    background-color: rgb(51, 51, 51, 0.5);
}

.banner__button:hover {
    color: #000;
    background-color: #e6e6e6;
    transition: all 0.2s;
}

.banner__fadeBotton {
    height: 20rem;
    background-image: linear-gradient(180deg, transparent, rgb(37, 37, 37, 0.61), #111);
}